import React from 'react';

import { Button, Paper, TextField, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import hermes from '../assets/hermes.png';
import pigeon from '../assets/pigeon.png';

import { applyCommand, StoreComponent } from '../utils/store';
import { default as allExercises } from '../assets/exercises';
import _ from 'lodash';

const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap'
  },
  passwordContainer: {
    flex: 2
  },
  imageContainer: {
    flexGrow: 5
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
  dense: {
    marginTop: 16,
  },
  menu: {
    width: 200,
  },
  button: {
    margin: theme.spacing.unit,
  },
  vBox: {
    display: 'flex',
    flexDirection: 'column',
    flex: 1,
    flexShrink: 1,
  },
  hBox: {
    display: 'flex',
    flexDirection: 'row',
    flexGrow: 1,
    flexShrink: 1
  }
});

class CenteredElement extends React.Component {
  render() {
    const spacer = <div style={{flex: 1}} />;
    return (
      <div className={this.props.class}>
        {spacer}
        {this.props.children}
        {spacer}
      </div>
    )
  }
}

class PasswordPage extends StoreComponent {
  constructor(props) {
    super(props);
    this.state = {
      password: ''
    };
  }
  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  }
  submitPassword() {
    const { mode } = this.props;
    const { [mode]: exercises = [] } = allExercises;

    const { [mode]: currentState = {} } = this.state.state;
    const { currentPassword } = currentState;

    if (!this.state.password) {
      return alert('Invalid Password');
    }

    const newPassword = this.state.password.toUpperCase();

    const currentExerciseIndex = !currentPassword ? -1 : exercises.findIndex(({password}) => password.toUpperCase() === currentPassword.toUpperCase());
    const newExerciseIndex = _.findIndex(exercises, ({password}) => password.toUpperCase() === newPassword);

    if (newExerciseIndex !== -1) {
      if (_.isEmpty(currentState) || newExerciseIndex > currentExerciseIndex) {
        applyCommand(state => ({
          ...state,
          exercises: {
            ...state.exercises,
            [mode]: {
              ...state.exercises[mode],
              latestPassword: newPassword
            }
          }
        }));
        alert("You've unlocked a new exercise!");
      }
    }

    this.setState({
      password: ''
    });
  }

  render() {
    const { classes, mode } = this.props;
    const backgroundUrl = mode === 'SATIRES' ? pigeon : hermes;
    const containerStyle = {
      backgroundImage: `url(${backgroundUrl})`, 
      backgroundRepeat: 'no-repeat', 
      backgroundPosition: 'center', 
      backgroundSize: '100%',
      height: 0,
      paddingTop: 'calc(100% / 1.6)', // Aspect ratio is 16:10
      position: 'relative'
    };
    const whiteboardStyle = {
      position: 'absolute', 
      top: '25%', 
      left: '0%', 
      width: '45%', 
      height: '75%', 
      overflow: 'auto', 
      fontSize: '1.25vw'
    };
    return (
      <div style={containerStyle}>
        <div style={whiteboardStyle}>
        <Paper style={{minWidth: 200}}>
            <CenteredElement class={classes.hBox}>
              <Typography variant="h6">
                Please sign here
              </Typography>
            </CenteredElement>
            <CenteredElement class={classes.hBox}>
              <TextField
              id="password"
              label="Password"
              type="password"
              className={classes.textField}
              value={this.state.password}
              onChange={this.handleChange('password')}
              onKeyPress={ev => {
                if (ev.key === 'Enter') {
                  this.submitPassword();
                }
              }}
              margin="normal"
              variant="outlined"
              />
            </CenteredElement>
            
            <CenteredElement class={classes.hBox}>
              <Button color="primary" className={classes.button} onClick={() => this.submitPassword()} value="submit">
                Login
              </Button>
            </CenteredElement>
           
          </Paper>
        </div>
      </div>
   );
  }
}




export default withStyles(styles, { withTheme: true })(PasswordPage);