import { BehaviorSubject, Subject } from 'rxjs';
import { map, withLatestFrom } from 'rxjs/operators';

import React from 'react';

console.log('Creating Store');
const defaultState = JSON.stringify({
  exercises: {}
});

const getLatestInternalState = () => {
  try {
    return JSON.parse(localStorage.getItem('classics-is-cool') || defaultState);
  } catch (e) {
    console.log('Error getting internal state, returning default');
    return JSON.parse(defaultState);
  }
}


const storeSubject = new BehaviorSubject(getLatestInternalState()); // Private
export const store$ = storeSubject.asObservable();
export const getLatestState = () => storeSubject.getValue();

const updateStore = state => storeSubject.next(state); // Private

const commandSubject = new Subject(); // Private
const command$ = commandSubject.asObservable(); // Private
export const applyCommand = command => commandSubject.next(command);

command$.pipe(
  withLatestFrom(store$),
  map(([command, state]) => command(state))
).subscribe(newState => {
  // console.log('Updating store', newState);
  updateStore(newState);
});

store$.subscribe(state => {
  // Update localstorage
  localStorage.setItem('classics-is-cool', JSON.stringify(state));
});

export const clearStore = () => {
  // console.log('Clearing the store');
  applyCommand(() => JSON.parse(defaultState));
}

export class StoreComponent extends React.Component {
  componentWillMount() {
    this.subscription = store$.subscribe(state => {
      this.setState({
        state
      });
    })
  }
  componentWillUnmount() {
    if (!!this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  getPersistedState() {
    return this.state.state;
  }
}