const unlockShop = () => {
  const { mode } = getParams();
  applyCommand(state => ({
    ...state,
    exercises: {
      [mode]: {
        ...state.exercises[mode],
        isShopUnlocked: true
      }
    }
  }));
  alert('The shop is now open for business!');
}

